import {
  HashRouter as Router,
  Switch,
  Route,
  Redirect,
} from "react-router-dom";
import Page from "./containers/Page";
import { Provider } from "react-redux";
import store from "./state";
import Data from "./data.json";
const App = () => {
  const pages = Data.entities.pages;
  const pagesArr = pages.allIds;
  return (
    <Provider store={store}>
      <Router>
        <Switch>
          {pagesArr.map((id, index) => {
            return (
              <Route key={index} exact path={`/${pages.byId[id].short}`}>
                <Page page={pages.byId[id]} />
              </Route>
            );
          })}
          <Redirect to={`/${pages.byId[pagesArr[0]].short}`} />;
        </Switch>
      </Router>
    </Provider>
  );
};

export default App;
