import createReducer from "./createReducer";
import * as types from "../constants/ActionTypes";
import { CHOICES_KEY } from "../constants/LocalStorage";

const addChoice = () => {};

const removeChoice = () => {};

const persistedChoices = localStorage.getItem(CHOICES_KEY);

const choiceReducer = createReducer(persistedChoices || [], {
  [types.ADD_CHOICE]: addChoice,
  [types.REMOVE_CHOICE]: removeChoice,
});
export default choiceReducer;
