import createReducer from "./createReducer";

//load in data from wordpress.
let data = {};

if (typeof rawFormData !== "undefined") {
  try {
    // eslint-disable-next-line no-undef
    data = JSON.parse(rawFormData);
  } catch (err) {
    data = {};
  }
}

const formDataReducer = createReducer(data, {});

export default formDataReducer;
